.shipping-container {
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
  margin: 20px;

  h1 {
    font-weight: bold;
    margin: 0;
  }

  form {
    background-color: #ffffff;
    display: flex;

    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 50px;
    height: 100%;
    width: 40%;
    text-align: center;
  }
  input {
    background-color: #eee;
    border: none;
    padding: 12px 15px;
    margin: 8px 0;
    width: 100%;
  }

  button {
    cursor: pointer;
    width: 100%;
    bottom: 12rem;
    right: 0px;
    color: #ffffff;
    border-radius: 1.6rem;
    background-color: #713434;
    padding: 1rem;
    transition: 0.4s;
  }

  button:active {
    transform: scale(0.95);
  }

  button:hover {
    cursor: pointer;
    width: 100%;
    bottom: 12rem;
    right: 0px;
    color: #ffffff;
    border-color: #a34b4b;
    padding: 1rem;
    background-color: #a34b4b;
  }

  button:focus {
    outline: none;
  }

  button.ghost {
    background-color: #ffffff;
    color: #713434;
  }
}
